<template>
<div :class="{block: disableCards == true || showCards == true}"></div>
  <div class="hello container SelectDisabled">
    <div class="box" @click="flip(item)" v-for="item in cards" :key="item.id">
            <div :class="{turn: item.flipped, shaking: item.wrongOne == true}" class="body">
                <div class="imgContainer">
                    <img class="SelectDisabled" :src="item.img" alt="">
                </div>
                <div class="content d-flex flex-column align-items-center justify-content-center">
                    <div>
                        <h3 class="text-white fs-5">{{item.title}}</h3>
                        <p class="fs-6 text-white">{{item.text}}</p>
                    </div>
                </div>
            </div>
        </div>   
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props:['restart'],
  data(){
    return{
      cards: [
      {
        id: 0,
        img: "https://picsum.photos/500/300",
        title: "1",
        //text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Explicabo sed cum neque, rem provident ex. Laboriosam perspiciatis modi eveniet in?",
        flipped: true,
        wrongOne: false,
      },
      {
        id: 1,
        img: "https://picsum.photos/500/300",
        title: "1",
        //text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Explicabo sed cum neque, rem provident ex. Laboriosam perspiciatis modi eveniet in?",
        flipped: false,
        wrongOne: false,
      }
      ],
      counter: 4,
      two: false,
      wrong: false,
      id: 0,
      lostCount: 0,
      disableCards: false,
      showCards: false,
    }
  },
  watch:{
    restart(){
      if(this.restart == true){
        this.counter = 4;
        this.cards = [];
        this.cards.push({
          id: 0,
          img: "https://picsum.photos/500/300",
          title: "1",
          //text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Explicabo sed cum neque, rem provident ex. Laboriosam perspiciatis modi eveniet in?",
          flipped: false,
          wrongOne: false,
        },
        {
          id: 1,
          img: "https://picsum.photos/500/300",
          title: "1",
          //text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Explicabo sed cum neque, rem provident ex. Laboriosam perspiciatis modi eveniet in?",
          flipped: false,
          wrongOne: false,
        })
        this.lostCount = 0
      }
    },
  },
  methods:{
    flip(item){
      const complete1 = this.cards.filter(task => task.flipped === true);
      if(complete1.length < 2) item.flipped = true;
      const complete = this.cards.filter(task => task.flipped === true);
      if(complete.length == 2){  
        this.disableCards = true;    
        let firstID = this.cards.findIndex(item => item.id == complete[0].id);
        let secondID = this.cards.findIndex(item => item.id == complete[1].id);
        setTimeout(() => {
          if(complete[0].title == complete[1].title){
            this.cards.splice(this.cards.findIndex(item => item.id == complete[0].id),1);
            this.cards.splice(this.cards.findIndex(item => item.id == complete[1].id),1);
        
            if(this.cards.length == 0){
              this.$emit('Process');
              setTimeout(()=> {
                this.showCards = true;
                this.two = false;
                for (let index = 0; index < this.counter; index++) {
                  if(index == (this.counter/2)){
                    
                    if(this.two == false){
                      index = 0;
                      this.two = true;
                    }
                  } 
                  if(this.two == true && index == (this.counter/2)){
                    this.randomize(this.cards)
                    break;
                  } 
                  this.id+=1;
                  this.cards.push({
                    id: this.id,
                    img: "https://picsum.photos/500/300",
                    title: index+1,
                    //text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Explicabo sed cum neque, rem provident ex. Laboriosam perspiciatis modi eveniet in?",
                    flipped: false,
                    wrongOne: false,
                  })                 
                }
                this.counter+=2;

                setTimeout(() => {                
                  this.cards.forEach(el => {
                    el.flipped = true;
                  });
                  setTimeout(() => {
                    this.cards.forEach(el1 => {
                      el1.flipped = false;
                    });
                    this.showCards = false;
                  },3000)
                },3000)
              },500)
            }
          }else{
            this.cards[firstID].wrongOne = true;
            this.cards[secondID].wrongOne = true;
            this.lostCount++;
            this.$emit('lost', this.lostCount);
          }
          this.cards.forEach(e => {
            e.flipped = false;
          });     
          setTimeout(() => {
            this.disableCards = false;
          },1000)              
        },1500);  
        this.cards[firstID].wrongOne = false;
        this.cards[secondID].wrongOne = false;  
             
      }
    },
    randomize(arr) {
      var i, j, tmp;
      for (i = arr.length - 1; i > 0; i--) {
          j = Math.floor(Math.random() * (i + 1));
          tmp = arr[i];
          arr[i] = arr[j];
          arr[j] = tmp;
      }
      return arr;
    }
  }
}
</script>

