<template>
  <div class="home noselect">
    <div class="blure-bg" v-if="isOpen == true"></div>
    <div class="nav">
      <div class="left">
        <h2>Level: {{level}}</h2>
      </div>
      <div class="mid"> 
        <!-- LOGO HERE -->
      </div>
      <div class="right">
        <div :class="{lostHeart: this.lostround > 0}" class="heart"></div>
        <div :class="{lostHeart: lostround > 1}" class="heart"></div>
        <div :class="{lostHeart: lostround > 2}" class="heart"></div>
      </div>
    </div>
    <hr>
    <HelloWorld 
      @lost="countLosses"
      @Process="gameStep"
      :restart="restart"
    />

    <teleport to="body">
      <div class="modal" v-if="isOpen">
        <Modal 
        @resGame="restartGame"
        :level="level"
        />
      </div>
    </teleport>
    
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import Modal from '@/components/Modal.vue'

export default {
  name: 'HomeView',
  components: {
    HelloWorld,
    Modal
  },
  data(){
    return{
      lostround: 0,
      isOpen: false,
      restart: false,
      level: 1,
    }
  },
  methods:{
    countLosses(i){    
      this.lostround = i;
      if(this.lostround == 3){
        this.isOpen = true;
      }
    },
    restartGame(){
      this.restart = true;
      this.isOpen = false;
      this.lostround = 0;
      this.level = 1;
    },
    gameStep(){
      this.restart = false,
      this.level++;
    }
  }
}
</script>

